import { css } from 'lit';

export const styles = css`[part=overlay]{background:var(--background-color)}[part=base]{border-radius:var(--border-radius);background:var(--background)}[part=mini]{background:var(--background)}:host([position=start]) [part=mini],:host([position=end]) [part=mini]{-webkit-box-shadow:var(--elevation);box-shadow:var(--elevation)}[part~=base],[part~=mini]{border-inline-end-color:var(--border-color)}:host([open]:not([position=relative])) [part=base]{-webkit-box-shadow:var(--elevation);box-shadow:var(--elevation)}`;
